"use client";

import React, { useState, useEffect, ReactNode, useCallback } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import {
  YadakchiAdminLayout,
  InfoCard,
  Label,
  ActionToolbar,
  EntityHeader,
  Badge,
  Button, // ایمپورت Button
  FormModal, // ایمپورت FormModal
  Input,     // ایمپورت Input
} from "@my-ds/design-system";

import { getWarrantyById, updateWarranty } from "../../services/api/warrantyService";
import { Warranty } from "@/services/api/types";
interface WarrantyDetailsPageProps {
  params: { id: string; }
}

export default function WarrantyDetailsPage({ params }: WarrantyDetailsPageProps) {
  const { id } = params;
  const router = useRouter();
  
  const [warranty, setWarranty] = useState<Warranty | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // State های مربوط به مودال ویرایش
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [formTitle, setFormTitle] = useState('');
  const [actionLoading, setActionLoading] = useState(false);
  
  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;
  
  // ۱. تابع واکشی را با useCallback پایدار می‌کنیم
  const fetchWarranty = useCallback(async () => {
    if (!id) return;
    try {
      setIsLoading(true);
      setError(null);
      const warrantyData = await getWarrantyById(Number(id));
      setWarranty(warrantyData);
      setFormTitle(warrantyData.title); // مقدار اولیه فرم را هم تنظیم می‌کنیم
    } catch (err) {
      setError(err instanceof Error ? err.message : "یک خطای ناشناخته رخ داد.");
    } finally {
      setIsLoading(false);
    }
  }, [id]);

  useEffect(() => {
    fetchWarranty();
  }, [fetchWarranty]);

  // --- Handlers ---
  const handleOpenEditModal = () => {
    if (warranty) {
      setFormTitle(warranty.title);
      setIsEditModalOpen(true);
    }
  };

  const handleUpdate = async () => {
    if (!warranty || !formTitle.trim()) return;
    setActionLoading(true);
    try {
      await updateWarranty(warranty.id, { title: formTitle });
      setIsEditModalOpen(false);
      // ۲. ✨ پس از آپدیت موفق، داده‌ها را دوباره واکشی می‌کنیم ✨
      await fetchWarranty(); 
    } catch (err) {
      alert("خطا در ویرایش گارانتی.");
    } finally {
      setActionLoading(false);
    }
  };

  const renderContent = () => {
    if (isLoading) return <p>در حال بارگذاری...</p>;
    if (error) return <p>خطا: {error}</p>;
    if (!warranty) return <p>یافت نشد.</p>;

    return (
      <>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
          <EntityHeader
            title={warranty.title}
            avatarFallback={warranty.title.charAt(0)}
          >
            <Badge 
              label={warranty.isActive ? "فعال" : "غیرفعال"}
              variant={warranty.isActive ? "success" : "neutral"}
            />
          </EntityHeader>

          <ActionToolbar onBackClick={() => router.back()}>
            {/* دکمه ویرایش حالا مودال را باز می‌کند */}
            <Button variant="primary" onClick={handleOpenEditModal}>
              ویرایش گارانتی
            </Button>
          </ActionToolbar>

          <InfoCard
            title="جزئیات گارانتی"
            items={[
              { label: "شناسه", value: String(warranty.id) },
              { label: "عنوان", value: warranty.title },
              { label: "وضعیت", value: warranty.isActive ? "فعال" : "غیرفعال" },
              { label: "تاریخ ایجاد", value: new Date(warranty.createDate).toLocaleString('fa-IR') },
              { label: "آخرین بروزرسانی", value: warranty.updateDate ? new Date(warranty.updateDate).toLocaleString('fa-IR') : "-" },
            ]}
          />
        </div>

        {/* مودال ویرایش */}
        <FormModal
            isOpen={isEditModalOpen}
            onClose={() => setIsEditModalOpen(false)}
            title="ویرایش عنوان گارانتی"
            onConfirm={handleUpdate}
            isLoading={actionLoading}
            confirmText="ذخیره تغییرات"
        >
            <form onSubmit={(e) => { e.preventDefault(); handleUpdate(); }}>
                <Input
                    id="warranty-title-edit"
                    label="عنوان جدید"
                    value={formTitle}
                    onChange={(e) => setFormTitle(e.target.value)}
                    required
                    autoFocus
                />
            </form>
        </FormModal>
      </>
    );
  };

  return (
    <YadakchiAdminLayout
      currentPath={pathname}
      renderLink={renderLink}
    >
      {renderContent()}
    </YadakchiAdminLayout>
  );
}